* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  line-height: 1.5;
  background: radial-gradient(rgb(178, 182, 182) 0.5px, transparent 5px);
  background-size: 35px 35px;
}
ul {
  list-style: none;
  padding: 0;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
  margin-top: 10px;
  border-radius: 5px;
}

#search-box:focus,
#search-box:hover {
  border-radius: 5px;
  border-color: transparent;
  border-image: linear-gradient(21deg, #10abff, #1beabd);
  box-shadow: inset 0 0 0 3px #fff, 0 0 0 4px #fff, 3px -3px 20px #1beabd, -3px 3px 20px #10abff;
}
.country-flag {
  width: 30px;
  height: 30px;
}
.country-elem {
  font-weight: bold;
}
.country-elem:not(:last-child) {
  margin-bottom: 10px;
}
.country-text {
  font-weight: 400;
  display: inline;
}

.countries-elem {
  display: flex;
  align-items: center;
}
.countries-flag {
  width: 30px;
  height: 30px;
}
.countries-text {
  display: inline;
  font-weight: 500;
  margin-left: 10px;
}
